<script>
  // import pdf from 'vue-pdf'
  export default {
    name: 'resource',
    // components: {
    //   pdf,
    // },
    data() {
      return {
        page: 1,
        // dates: ['2020-09-10', '2020-09-20'],
        // date: new Date().toISOString().substr(0, 7),
        menu: false,
        flag14: false,
        items: ["试卷", "资料"],
        choose_item: 0,
        name: '',
        date: '',
        duration: '',
        len: '',
        pk: 0,
        name2: '',
        date2: '',
        path2: '',
        file_choosed: 0,
        dialog2: false,
        len2: '',
        flag101: 0,

      }
    },
    created() {
      this.get_class_papers()
      this.get_class_files()
    },
    computed: {
      dateRangeText () {
        return this.dates.join(' ~ ')
      },
      href () {
        console.log("http://hyperlimit.cn" + this.files[this.file_choosed].file.path.slice(7))
        return "http://hyperlimit.cn" + this.files[this.file_choosed].file.path.slice(7)
      },
      src () {
        console.log("https://view.officeapps.live.com/op/view.aspx?src=http://hyperlimit.cn" + this.files[this.file_choosed].file.path.slice(7))
        return "https://view.officeapps.live.com/op/view.aspx?src=http://hyperlimit.cn" + this.files[this.file_choosed].file.path.slice(7)
      },
      // ifpdf () {
      //   var that = this;
      //   if (that.flag101 == 1) {
      //     if (that.files[that.file_choosed].file.path.slice(-1) == 'f' || that.files[that.file_choosed].file.path.slice(-1) == 'F') {
      //       return 0
      //     } else {
      //       return 1
      //     }
      //   } else {
      //     return 0
      //   }
      // }
    },
    methods: {
      get_class_papers: function () {
        var that = this;
        this.$axios({
          method: 'get',
          url: '/api/get_class_papers/',
          params: {
            id: that.$global.userMsg.id,
          },
        })
        .then(function (response) {
          if (response.data.error_num == 0) {
            console.log("资料", response.data)
            if (response.data.papers.length > 0) {
              that.papers = response.data.papers.reverse()
              that.len = response.data.papers[0].paper.questions.length
              that.name = response.data.papers[0].paper.name
              that.duration = response.data.papers[0].duration
              that.date = response.data.papers[0].time.slice(0, 10)
            }
            that.flag14 = true
          } else {
            alert("获取资料失败！")
          }
        })
      },
      get_class_files: function () {
        var that = this;
        this.$axios({
          method: 'get',
          url: '/api/get_class_files/',
          params: {
            id: that.$global.userMsg.id,
          },
        })
        .then(function (response) {
          if (response.data.error_num == 0) {
            console.log("资料", response.data)
            if (response.data.files.length > 0) {
              that.files = response.data.files.reverse()
              // that.len2 = response.data.files[0].length
              // that.name = response.data.files[0].file.name
              // that.duration = response.data.files[0].duration
              // that.date = response.data.files[0].time.slice(0, 10)
              setTimeout(() => {
                that.flag101 = 1
              }, 300)
            }
            that.flag14 = true
          } else {
            alert("获取资料失败！")
          }
        })
      },
      choose: function (index) {
        this.pk = index;
        this.name = this.papers[index].paper.name;
        this.date = this.papers[index].time.slice(0, 10);
        this.duration = this.papers[index].duration;
        this.len = this.papers[index].paper.questions.length;
      },
      choose2: function (index) {
        this.pk2 = index;
        this.name2 = this.files[index].file.name;
        this.date2 = this.files[index].file.edit_date;
        this.path2 = this.files[index].file.path;
        // this.star2 = this.files[index].star2;
      },
      jump: function () {
        var that = this;
        let q_id = new Array();
        this.$global.flag = ''
        this.$global.paper = this.papers[this.pk].paper.questions
        this.$global.len = this.papers[this.pk].paper.questions.length
        this.$global.duration = this.papers[this.pk].duration
        this.$global.paper_name = this.papers[this.pk].name
        for (var i = 0; i < that.papers[that.pk].paper.questions.length; i++) {
          q_id[i] = that.papers[that.pk].paper.questions[i].id
        }
        setTimeout(() => {
          this.$global.q_id = q_id
          this.$router.push({
            path: './precisetrainpage'
          })
        }, 300)
      },
      open: function (index) {
        console.log("index:", index)
        var that = this;
        // console.log(src)
        if (that.files[index].file.path.slice(-1) == 'f' || that.files[index].file.path.slice(-1) == 'F') {
          window.open("http://hyperlimit.cn" + that.files[index].file.path.slice(7), "_blank");
        } else {
          window.open("https://view.officeapps.live.com/op/view.aspx?src=http://hyperlimit.cn" + that.files[index].file.path.slice(7))
        }
      }
    }
